import { Inject, Injectable, LOCALE_ID } from '@angular/core';
import { getLocaleNumberSymbol, NumberSymbol } from '@angular/common';

import { Currency, PayeeSummary, Payment, OneOffPayment, Payee } from '@backbase/data-ang/billpay';
import { Schedule, EndTypes, FrequencyKeys, PaymentServicesTypes, PaymentFormState, PaymentReview } from '../model';

import { recurringFrequencies } from '../constants';

@Injectable()
export class BillpayPaymentParserService {
  private readonly decimalSeparator: string;
  constructor(@Inject(LOCALE_ID) private readonly locale: string) {
    this.decimalSeparator = getLocaleNumberSymbol(this.locale, NumberSymbol.CurrencyDecimal);
  }
  paymentRecurringToFormState(payment: Payment, payees: PayeeSummary[]): PaymentFormState {
    const frequency = payment.recurring && recurringFrequencies.find(item => item.value === payment.frequency);
    const endType = payment.numberOfInstances ? EndTypes.After : EndTypes.Never;

    return {
      ...this.parsePaymentCommonResponse(payment, payees),
      schedule: {
        repeat: payment.numberOfInstances,
        endType,
        startDate: payment.paymentDate,
        alertPendingCheckbox: payment.paymentScheduledAlert || false,
        alertSentCheckbox: payment.paymentSentAlert || false,
        alertLastPendingCheckbox: payment.modelExpirationAlert || false,
        ...(frequency && { frequency: frequency.value }),
      },
    } as PaymentFormState;
  }

  paymentOneOffToFormState(payment: OneOffPayment, payees: PayeeSummary[]): PaymentFormState {
    return {
      ...this.parsePaymentCommonResponse(payment, payees),
      schedule: {
        startDate: payment.paymentDate,
        alertPendingCheckbox: false,
        alertSentCheckbox: false,
        alertLastPendingCheckbox: false,
        frequency: FrequencyKeys.Once,
      },
    } as PaymentFormState;
  }

  paymentFormStateToReview({
    accountFrom,
    payeeTo,
    amount,
    memo,
    schedule,
    overnightDeliveryAddress,
  }: PaymentFormState): PaymentReview {
    return {
      amount: {
        amount: amount.amount.replace(this.decimalSeparator, '.'),
        currencyCode: amount.currency,
      },
      paymentDate: schedule.paymentServiceDate || schedule.startDate,
      paymentAccount: accountFrom,
      recurring: schedule.frequency !== FrequencyKeys.Once,
      ...(memo && { paymentMemo: memo }),
      ...(overnightDeliveryAddress && { overnightDeliveryAddress }),
      ...this.parsePaymentFormPayee(payeeTo),
      ...this.parsePaymentFormSchedule(schedule),
    };
  }

  paymentAndPayeeResponsesToReview(payment: Payment | OneOffPayment, payee: Payee): PaymentReview {
    return {
      ...this.paymentResponseToReview(payment),
      ...(payee.address && { address: payee.address }),
    };
  }
  // eslint-disable-next-line no-unused-vars, @typescript-eslint/no-unused-vars
  private paymentResponseToReview({ id, status, ebillID, ...rest }: Payment | OneOffPayment) {
    return rest;
  }

  private parsePaymentFormPayee(
    payee: PayeeSummary,
  ): Pick<PaymentReview, 'payeeID' | 'payeeName' | 'payeeAccountNumber' | 'payeeNickName'> {
    return {
      payeeID: payee.id,
      payeeName: payee.name,
      ...(payee.accountNumber && { payeeAccountNumber: payee.accountNumber }),
      ...(payee.nickName && { payeeNickName: payee.nickName }),
    };
  }

  private parsePaymentFormSchedule(
    schedule: Schedule,
  ): Pick<
    PaymentReview,
    'fee' | 'numberOfInstances' | 'frequency' | 'paymentScheduledAlert' | 'paymentSentAlert' | 'modelExpirationAlert'
  > {
    return {
      ...(this.getScheduleFee(schedule) && { fee: this.getScheduleFee(schedule) }),
      ...(schedule.repeat && { numberOfInstances: schedule.repeat }),
      ...(schedule.frequency !== FrequencyKeys.Once && { frequency: schedule.frequency }),
      ...(schedule.alertPendingCheckbox && { paymentScheduledAlert: schedule.alertPendingCheckbox }),
      ...(schedule.alertSentCheckbox && { paymentSentAlert: schedule.alertSentCheckbox }),
      ...(schedule.alertLastPendingCheckbox && { modelExpirationAlert: schedule.alertLastPendingCheckbox }),
    };
  }

  private getScheduleFee(schedule: Schedule): Currency | undefined {
    return (
      (schedule.paymentService &&
        (schedule.paymentService.index === PaymentServicesTypes.Expedited ||
          schedule.paymentService.index === PaymentServicesTypes.Overnight) &&
        schedule.paymentService.service &&
        schedule.paymentService.service.fee) ||
      undefined
    );
  }

  private parsePaymentCommonResponse(
    payment: Payment | OneOffPayment,
    payees: PayeeSummary[],
  ): Partial<PaymentFormState> {
    const payeeTo = payees.find(item => item.id === payment.payeeID);
    const amount = payment.amount && {
      currency: payment.amount.currencyCode,
      amount: payment.amount.amount.replace(this.decimalSeparator, '.'),
    };
    return {
      paymentId: payment.id,
      ...(payment.amount && { amount }),
      ...(payeeTo && { payeeTo }),
      ...(payment.paymentAccount && { accountFrom: payment.paymentAccount }),
      ...(payment.paymentMemo && { memo: payment.paymentMemo }),
    };
  }
}
