import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { ENROLMENT_PATH } from '../constants';
import { BillpayEnrolmentService } from './billpay-enrolment.service';

@Injectable({ providedIn: 'root' })
export class BillpayEnrolmentGuard implements CanActivate {
  constructor(private readonly router: Router, private readonly enrolmentService: BillpayEnrolmentService) {}

  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot,
  ): Observable<boolean> | Promise<boolean> | boolean {
    return this.enrolmentService.isEnrolled().pipe(
      map((enrolled: boolean) => {
        const currentRoute = this.enrolmentService.getCurrentRoute();

        if (!enrolled && !currentRoute?.endsWith(ENROLMENT_PATH)) {
          this.enrolmentService.previousRoute.next(state.url);
          const parentRoutes = route.pathFromRoot.map(snapshot => snapshot?.routeConfig?.path).filter(item => !!item);
          this.router.navigate([...parentRoutes, ENROLMENT_PATH]);
          return false;
        }
        return true;
      }),
    );
  }
}
