import {
  Component,
  OnInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  ViewChild,
  TemplateRef,
  Input,
} from '@angular/core';
import { first } from 'rxjs/operators';
import { BillpayEnrolmentService } from '../../services/billpay-enrolment.service';
import { NotificationService } from '@backbase/ui-ang/notification';

@Component({
  selector: 'bb-billpay-enrolment',
  templateUrl: './billpay-enrolment.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BillpayEnrolmentComponent implements OnInit {
  enrolmentFailed = false;
  loading = false;

  @Input() notificationDismissTime?: number;

  @ViewChild('successHeader') notificationHeaderSuccessTemplate: TemplateRef<any> | string = '';
  @ViewChild('errorHeader') notificationHeaderErrorTemplate: TemplateRef<any> | string = '';

  constructor(
    private readonly billpayEnrolmentService: BillpayEnrolmentService,
    private readonly cd: ChangeDetectorRef,
    private readonly notificationService: NotificationService,
  ) {}

  ngOnInit() {
    this.loading = true;

    this.billpayEnrolmentService
      .isEnrolled()
      .pipe(first())
      .subscribe(
        isEnrolled => {
          if (isEnrolled) {
            this.loading = false;
            this.redirectUser();
          } else {
            this.enrolUser();
          }
        },
        () => {
          this.onEnrolFail();
          this.cd.markForCheck();
        },
      );
  }

  enrolUser() {
    this.loading = true;
    this.enrolmentFailed = false;
    this.billpayEnrolmentService
      .enrolUser()
      .pipe(first())
      .subscribe(isEnrolled => {
        if (isEnrolled) {
          this.onEnrolSuccess();
        } else {
          this.onEnrolFail();
        }
        this.cd.markForCheck();
      });
  }

  onEnrolSuccess() {
    this.loading = false;
    this.notificationService.showNotification({
      header: this.notificationHeaderSuccessTemplate,
      message: '',
      modifier: 'success',
      ttl: this.notificationDismissTime,
    });
    this.redirectUser();
  }

  onEnrolFail() {
    this.loading = false;
    this.enrolmentFailed = true;
    this.notificationService.showNotification({
      header: this.notificationHeaderErrorTemplate,
      message: '',
      modifier: 'error',
      ttl: this.notificationDismissTime,
    });
  }

  redirectUser() {
    this.billpayEnrolmentService.previousRoute.pipe(first()).subscribe(route => {
      this.billpayEnrolmentService.redirectUser(route || '');
    });
  }
}
