import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { BillpayPaymentScheduleService } from '../../services/billpay-payment-schedule.service';
import { FrequencyKeys, PaymentReview, AccountNumberFormat } from '../../model';
import { accountNumberMaskedFormat, accountNumberFullFormat } from '../../constants';

@Component({
  selector: 'bb-billpay-payment-review-card',
  templateUrl: './billpay-payment-review-card.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BillpayPaymentReviewCardComponent {
  /**
   * Details of the payment to be reviewed before creation.
   */
  @Input() paymentReview?: PaymentReview;
  /**
   * Whether the account number should be masked. Defaults to true.
   */
  @Input() accountNumberMask = true;

  /**
   * Frequency Label based on configuration
   */
  @Input() frequencyLabel?: any;

  /** @internal */
  constructor(private readonly scheduleService: BillpayPaymentScheduleService) {}

  get totalAmount(): number {
    const fee = Number(this.paymentReview && this.paymentReview.fee && this.paymentReview.fee.amount) || 0;
    const amount = Number(this.paymentReview && this.paymentReview.amount && this.paymentReview.amount.amount) || 0;
    return fee + amount;
  }

  get accountNumberFormatConfig(): AccountNumberFormat {
    return this.accountNumberMask ? accountNumberMaskedFormat : accountNumberFullFormat;
  }

  getEndDate(paymentDate: string, numberOfInstances: number, frequency: string) {
    return this.scheduleService.estimatedEndDate(paymentDate, frequency as FrequencyKeys, numberOfInstances);
  }
}
