import { PaymentServiceRadio } from './billpay-payment-service.model';

export interface Schedule {
  frequency: FrequencyKeys;
  startDate: string;
  sendDate?: string;
  endDate?: string;
  repeat?: number;
  endType?: string;
  paymentService?: PaymentServiceRadio;
  paymentServiceDate?: string;
  alertPendingCheckbox: boolean;
  alertSentCheckbox: boolean;
  alertLastPendingCheckbox: boolean;
}

export enum EndTypes {
  Never = 'never',
  On = 'on',
  After = 'after',
}

export interface Frequency {
  value: FrequencyKeys | string;
  name: string;
}

export enum FrequencyKeys {
  Once = 'ONCE',
  Weekly = 'WEEKLY',
  Biweekly = 'EVERY_2_WEEKS',
  Monthly = 'MONTHLY',
  EveryTwoWeeks = 'EVERY_2_WEEKS',
  SemiMonthly = 'TWICE_A_MONTH',
  EveryFourWeeks = 'EVERY_4_WEEKS',
  EveryTwoMonths = 'EVERY_2_MONTHS',
  Quarterly = 'EVERY_3_MONTHS',
  EverySixMonths = 'EVERY_6_MONTHS',
  Annually = 'ANNUALLY',
}
