import { Injectable, Inject, LOCALE_ID } from '@angular/core';
import { Observable, combineLatest, of, BehaviorSubject } from 'rxjs';
import { map, tap, catchError, switchMap } from 'rxjs/operators';
import {
  BillPayPaymentsService,
  BillPayPayeesSummaryService,
  BillPayAccountsService,
  PaymentRequest,
  RecurringPayment,
  OneOffPayment,
  BillPayAccountsGetResponseBody,
  BillPayPayeesSummaryGetResponseBody,
  RecurringPaymentByIdPutResponseBody,
  BillPayRecurringPaymentsPostResponseBody,
  PaymentByIdPutResponseBody,
  BillPayPaymentsPostResponseBody,
  Payment,
} from '@backbase/data-ang/billpay';
import { PaymentFormState, FrequencyKeys } from '../model';
import { formatDate } from '@angular/common';
import { getLocaleNumberSymbol, NumberSymbol } from '@angular/common';
import { LoadingState } from '../model';

@Injectable()
export class BillpayPaymentDataService {
  private currentPayeesLoadingState = LoadingState.Inactive;
  private currentAccountsLoadingState = LoadingState.Inactive;
  private readonly reload = new BehaviorSubject(undefined);
  private readonly decimalSeparator: string;

  /** @internal */
  constructor(
    private readonly paymentData: BillPayPaymentsService,
    private readonly accountsData: BillPayAccountsService,
    private readonly payeesSummaryData: BillPayPayeesSummaryService,
    @Inject(LOCALE_ID) private readonly locale: string,
  ) {
    this.decimalSeparator = getLocaleNumberSymbol(this.locale, NumberSymbol.CurrencyDecimal);
  }

  get payeesLoadingState(): LoadingState {
    return this.currentPayeesLoadingState;
  }

  get accountsLoadingState(): LoadingState {
    return this.currentAccountsLoadingState;
  }

  getOneOffPayment(id: string): Observable<OneOffPayment> {
    return this.paymentData.getPaymentById({ id }).pipe(map(body => body.payment));
  }

  getRecurringPayment(id: string): Observable<Payment> {
    return this.paymentData.getRecurringPaymentById({ id }).pipe(map(body => body.payment));
  }

  getPayeesAndAccounts(): Observable<BillPayPayeesSummaryGetResponseBody & BillPayAccountsGetResponseBody> {
    return combineLatest([this.getPayees(), this.getAccounts()]).pipe(
      map(
        ([payeeSummaries, accounts]) =>
          ({ ...payeeSummaries, ...accounts } as BillPayPayeesSummaryGetResponseBody & BillPayAccountsGetResponseBody),
      ),
    );
  }

  createPayment(
    payment: PaymentFormState,
  ): Observable<BillPayPaymentsPostResponseBody | BillPayRecurringPaymentsPostResponseBody> {
    return payment.schedule.frequency === FrequencyKeys.Once
      ? this.paymentData.postBillPayPayments({
          billPayPaymentsPostRequestBody: { payments: [this.toPaymentRequest(payment)] },
        })
      : this.paymentData.postBillPayRecurringPayments({
          billPayRecurringPaymentsPostRequestBody: { payment: this.toRecurringPaymentRequest(payment) },
        });
  }

  updatePaymentRecurring(payment: PaymentFormState, id: string): Observable<RecurringPaymentByIdPutResponseBody> {
    return this.paymentData.putRecurringPaymentById({
      id,
      recurringPaymentByIdPutRequestBody: { payment: this.toRecurringPaymentRequest(payment) },
    });
  }

  updatePaymentOneOff(payment: PaymentFormState, id: string): Observable<PaymentByIdPutResponseBody> {
    return this.paymentData.putPaymentById({
      id,
      paymentByIdPutRequestBody: { payment: this.toPaymentRequest(payment) },
    });
  }

  private getPayees(): Observable<BillPayPayeesSummaryGetResponseBody | undefined> {
    return this.reload.asObservable().pipe(
      tap(() => (this.currentPayeesLoadingState = LoadingState.Loading)),
      switchMap(() => this.payeesSummaryData.getBillPayPayeesSummary()),
      tap(() => (this.currentPayeesLoadingState = LoadingState.Inactive)),
      catchError(() => {
        this.currentPayeesLoadingState = LoadingState.Error;
        return of(undefined);
      }),
    );
  }

  private getAccounts(): Observable<BillPayAccountsGetResponseBody | undefined> {
    return this.reload.asObservable().pipe(
      tap(() => (this.currentAccountsLoadingState = LoadingState.Loading)),
      switchMap(() => this.accountsData.getBillPayAccounts()),
      tap(() => (this.currentAccountsLoadingState = LoadingState.Inactive)),
      catchError(() => {
        this.currentAccountsLoadingState = LoadingState.Error;
        return of(undefined);
      }),
    );
  }

  private toShortISODate(date: string): string {
    return formatDate(date, 'yyyy-MM-dd', this.locale);
  }

  private toPaymentRequest({
    payeeTo: { id: payeeID },
    accountFrom: { accountNumber, routingNumber, accountType, id },
    amount,
    memo,
    schedule,
    overnightDeliveryAddress,
  }: PaymentFormState): PaymentRequest {
    return {
      payeeID,
      paymentAccount: {
        ...(id && { id: id }),
        accountNumber,
        routingNumber,
        accountType,
      },
      ...(schedule.paymentService &&
        schedule.paymentService.service && { paymentServiceType: schedule.paymentService.service.paymentServiceType }),
      amount: {
        amount: amount.amount.replace(this.decimalSeparator, '.'),
        currencyCode: amount.currency,
      },
      paymentDate: this.toShortISODate(schedule.startDate),
      ...(memo && { paymentMemo: memo }),
      ...(overnightDeliveryAddress && {
        overnightDeliveryAddress: {
          address1: overnightDeliveryAddress.address1,
          state: overnightDeliveryAddress.state,
          city: overnightDeliveryAddress.city,
          postalCode: overnightDeliveryAddress.postalCode,
          ...(overnightDeliveryAddress.address2 && { address2: overnightDeliveryAddress.address2 }),
        },
      }),
    };
  }

  private toRecurringPaymentRequest({
    payeeTo: { id: payeeID },
    accountFrom: { accountNumber, routingNumber, accountType, id },
    amount,
    schedule,
    memo,
  }: PaymentFormState): RecurringPayment {
    return {
      payeeID,
      paymentAccount: {
        ...(id && { id: id }),
        accountNumber,
        routingNumber,
        accountType,
      },
      amount: {
        amount: amount.amount.replace(this.decimalSeparator, '.'),
        currencyCode: amount.currency,
      },
      ...(memo && { paymentMemo: memo }),
      paymentDate: this.toShortISODate(schedule.startDate),
      ...(schedule.repeat && { numberOfInstances: schedule.repeat }),
      frequency: schedule.frequency,
      paymentScheduledAlert: schedule.alertPendingCheckbox,
      paymentSentAlert: schedule.alertSentCheckbox,
      modelExpirationAlert: schedule.alertLastPendingCheckbox,
    };
  }
}
