import { Injectable } from '@angular/core';
import { BillPayEnrolmentService } from '@backbase/data-ang/billpay';
import { catchError, map } from 'rxjs/operators';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { ENROLMENT_PATH } from '../constants';

@Injectable({ providedIn: 'root' })
export class BillpayEnrolmentService {
  previousRoute = new BehaviorSubject<string | undefined>(undefined);

  constructor(
    private readonly dataService: BillPayEnrolmentService,
    private readonly route: ActivatedRoute,
    private readonly router: Router,
  ) {}

  enrolUser(): Observable<boolean> {
    return this.dataService.postBillPayEnrolment('response').pipe(
      map(response => response.status === 204),
      catchError(error => of(error.status === 204)),
    );
  }

  isEnrolled(): Observable<boolean> {
    return this.dataService.getBillPayEnrolment().pipe(
      map(body => body.enrolment.enrolled),
      catchError(() => of(false)),
    );
  }

  getCurrentRoute(): string | undefined {
    return this.getRoutePath(this.route, '');
  }

  redirectUser(previousRoute?: string) {
    if (previousRoute && !previousRoute.includes(ENROLMENT_PATH)) {
      this.router.navigateByUrl(previousRoute);
    } else {
      this.router.navigate(['./']);
    }
  }

  private readonly getRoutePath = (route: ActivatedRoute, path: string): string => {
    if (route.snapshot && route.snapshot.routeConfig && route.snapshot.routeConfig.path) {
      path += '/' + route.snapshot.routeConfig.path;
    }
    if (!route.firstChild) {
      return path;
    }
    return this.getRoutePath(route.firstChild, path);
  };
}
