import { Injectable } from '@angular/core';
import {
  BillPayPayeesService,
  BillPayPayverisService,
  BillPayEbillsService,
  BillPayPayeesSummaryService,
  BillPayPayeesSummaryGetResponseBody,
  BillPayPayverisResolveEbillErrorsPostRequestBody,
  BillPayPayverisResolveEbillErrorsPostResponseBody,
  PayeeSummary,
} from '@backbase/data-ang/billpay';
import { catchError, map, switchMap, tap } from 'rxjs/operators';
import { HttpResponse } from '@angular/common/http';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { PayeeSummariesByEbill } from '../model';
export { PayeeSummary, Currency, BillPayPayeesSummaryGetResponseBody } from '@backbase/data-ang/billpay';

export const defaultDeleteParams = {
  cancelPendingPayments: false,
};

@Injectable()
export class BillpayPayeeDataService {
  private readonly selectedDeletePayeeSource = new BehaviorSubject<PayeeSummary | undefined>(undefined);
  private readonly selectedDeleteEbillsSource = new BehaviorSubject<PayeeSummary | undefined>(undefined);
  private readonly reload = new BehaviorSubject(undefined);
  private buttonsDisabled = false;
  private currentPayeesLoadingState: 'loading' | 'error' | '' = '';
  private iterablePayeeSummaryList: PayeeSummary[] = [];

  /** @internal */
  constructor(
    private readonly data: BillPayPayeesService,
    private readonly payverisData: BillPayPayverisService,
    private readonly ebillsData: BillPayEbillsService,
    private readonly payeesSummaryData: BillPayPayeesSummaryService,
  ) {}

  deletePayee(id: string, electronic: boolean): Observable<HttpResponse<any>> {
    return electronic
      ? this.data.deleteElectronicPayeeById({ ...defaultDeleteParams, id })
      : this.data.deletePayeeById({ ...defaultDeleteParams, id });
  }

  resolveErrors(id: string): Observable<BillPayPayverisResolveEbillErrorsPostResponseBody> {
    const resolveErrorsRequest: BillPayPayverisResolveEbillErrorsPostRequestBody = {
      payeeID: id,
    };
    return this.payverisData.postBillPayPayverisResolveEbillErrors({
      billPayPayverisResolveEbillErrorsPostRequestBody: resolveErrorsRequest,
    });
  }

  deleteEbills(id: string): Observable<HttpResponse<any>> {
    return this.ebillsData.deleteBillPayEbills({ id });
  }

  getPayees(): Observable<PayeeSummariesByEbill | undefined> {
    return this.reload.asObservable().pipe(
      tap(() => (this.currentPayeesLoadingState = 'loading')),
      switchMap(() => this.payeesSummaryData.getBillPayPayeesSummary().pipe(map(groupByEbills))),
      tap(data => sortByPayeeGroup(data)),
      tap(data => this.createIterablePayeeSummaryList(data)),
      tap(() => {
        this.currentPayeesLoadingState = '';
      }),
      catchError(() => {
        this.currentPayeesLoadingState = 'error';
        return of(undefined);
      }),
    );
  }

  private createIterablePayeeSummaryList(list: PayeeSummariesByEbill) {
    this.iterablePayeeSummaryList = [];
    for (let i = 0; i < list.ebillsEnabled.length; i++) {
      const summary = list.ebillsEnabled[i];
      this.iterablePayeeSummaryList.push(summary);
    }
    for (let j = 0; j < list.ebillsNotEnabled.length; j++) {
      const summary = list.ebillsNotEnabled[j];
      this.iterablePayeeSummaryList.push(summary);
    }
  }

  reloadPayees() {
    this.reload.next(undefined);
  }

  disableWidgetButtons() {
    this.buttonsDisabled = true;
  }

  enableWidgetButtons() {
    this.buttonsDisabled = false;
  }

  updateSelectedDeletePayee(payee: PayeeSummary | undefined) {
    this.selectedDeletePayeeSource.next(payee);
  }
  updateSelectedDeleteEbills(payee: PayeeSummary | undefined) {
    this.selectedDeleteEbillsSource.next(payee);
  }
  get selectedDeletePayee(): Observable<PayeeSummary | undefined> {
    return this.selectedDeletePayeeSource.asObservable();
  }
  get selectedDeleteEbills(): Observable<PayeeSummary | undefined> {
    return this.selectedDeleteEbillsSource.asObservable();
  }

  get payeesLoadingState() {
    return this.currentPayeesLoadingState;
  }

  get areButtonsDisabled(): boolean {
    return this.buttonsDisabled;
  }

  get numberOfPayees() {
    return this.iterablePayeeSummaryList.length;
  }

  private payeesLoaded(): Promise<boolean> {
    const _this = this;
    return new Promise<boolean>(function (resolve) {
      (function waitUntilLoaded() {
        if (_this.currentPayeesLoadingState === '') return resolve(true);
        setTimeout(waitUntilLoaded, 100);
      })();
    });
  }

  async findPayeeSummaryById(payeeId: string): Promise<PayeeSummary | undefined> {
    if (await this.payeesLoaded()) {
      return this.iterablePayeeSummaryList.find(summary => summary.id === payeeId);
    }
    return;
  }
}

function groupByEbills(data: BillPayPayeesSummaryGetResponseBody): PayeeSummariesByEbill {
  const payeeSummaries = data.payeeSummaries as PayeeSummary[];
  return payeeSummaries.reduce(
    (grouped: any, payeeSummary: PayeeSummary) => {
      grouped[payeeSummary.ebill && payeeSummary.ebill.enabled ? 'ebillsEnabled' : 'ebillsNotEnabled'].push(
        payeeSummary,
      );
      return grouped;
    },
    {
      ebillsEnabled: [],
      ebillsNotEnabled: [],
    },
  );
}

function sortByPayeeGroup(data: PayeeSummariesByEbill) {
  sortPayeeSummaries(data.ebillsEnabled);
  sortPayeeSummaries(data.ebillsNotEnabled);
}

function sortPayeeSummaries(summaries: PayeeSummary[]) {
  summaries.sort((a, b) =>
    getPayeeDisplayName(a.name, a.nickName).toLowerCase() > getPayeeDisplayName(b.name, b.nickName).toLowerCase()
      ? 1
      : -1,
  );
}

function getPayeeDisplayName(payeeName: string, payeeNickName?: string): string {
  return payeeNickName || payeeName;
}
