import { Component, Input, OnDestroy } from '@angular/core';
import { BillpayUtilityService as Utility } from '../../services/billpay-utility.service';
import { BillPayPayverisEbillsEnrolmentPostResponseBody, PayeeSummary } from '@backbase/data-ang/billpay';
import { NgbDropdownConfig } from '@ng-bootstrap/ng-bootstrap';
import { ONE_OFF_PAYMENT, RECURRING_PAYMENT } from '../../constants';
import { BillpayPayeeDataService } from '../../services/billpay-payee-data.service';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({ template: '' })
export abstract class BillpayPayeeSummaryItemBaseComponent implements OnDestroy {
  /**
   * This method will be overriden by parent components
   */
  abstract handleGetEbills(): void;
  /**
   * Details of the payee summary.
   */
  @Input() payeeSummary: PayeeSummary | undefined;
  RECURRING_PAYMENT = RECURRING_PAYMENT;
  ONE_OFF_PAYMENT = ONE_OFF_PAYMENT;
  readonly ebillRetrieveErrorId = Utility.generateId();
  readonly noEbillsDueId = Utility.generateId();
  readonly ebillsAvailableId = Utility.generateId();
  readonly recurringPaymentScheduledIdId = Utility.generateId();
  readonly ebillPaymentScheduledId = Utility.generateId();
  readonly ebillDueOnId = Utility.generateId();
  readonly ebillSupportErrorId = Utility.generateId();
  readonly ebillUserActionErrorId = Utility.generateId();
  readonly ebillUnknownErrorId = Utility.generateId();
  readonly payeeAmountId = Utility.generateId();

  readonly destroy$ = new Subject();

  /** @internal */
  constructor(
    private readonly dataService: BillpayPayeeDataService,
    protected readonly dropdownButtonConfig: NgbDropdownConfig,
  ) {
    dropdownButtonConfig.placement = 'bottom-right';
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  handleStopEbills($event: Event): void {
    $event.preventDefault();
    this.dataService.updateSelectedDeleteEbills(this.payeeSummary);
  }

  handleDeletePayee($event: Event): void {
    $event.preventDefault();
    const payee = (this.payeeSummary && !this.payeeSummary.nextPayment && this.payeeSummary) || undefined;

    this.dataService.updateSelectedDeletePayee(payee);
  }

  get payeeName(): string {
    return (this.payeeSummary && (this.payeeSummary.nickName || this.payeeSummary.name)) || '';
  }

  get payeeId(): string {
    return (this.payeeSummary && this.payeeSummary.id) || '';
  }

  get areButtonsDisabled(): boolean {
    return this.dataService.areButtonsDisabled;
  }

  get areRecurringPaymentsScheduled(): boolean {
    const payeeSummary = this.payeeSummary;
    return !!(payeeSummary && payeeSummary.hasRecurringPayments);
  }

  get areEbillsInErrorState(): boolean {
    return this.isEbillsStatusInError();
  }

  get isEbillsErrorUnknown(): boolean {
    return this.ebillErrorEquals('ERROR');
  }

  get isEbillsErrorUpdateCredentials(): boolean {
    return this.ebillErrorEquals('UPDATE_CREDENTIALS');
  }

  get isEbillsErrorNotSupported(): boolean {
    return this.ebillErrorEquals('NOT_SUPPORTED');
  }

  get isEbillsErrorUserAction(): boolean {
    return this.ebillErrorEquals('USER_ACTION');
  }

  handleResolveEbillsErrors(): void {
    if (this.payeeSummary) {
      this.dataService
        .resolveErrors(this.payeeSummary.id)
        .pipe(takeUntil(this.destroy$))
        .subscribe(response => {
          if (this.isEbillEnrolmentFlowRequired(response)) {
            this.handleGetEbills();
          } else {
            this.dataService.reloadPayees();
          }
        });
    }
  }

  private isEbillEnrolmentFlowRequired(ebillEnrolmentResponse: BillPayPayverisEbillsEnrolmentPostResponseBody) {
    return (
      (ebillEnrolmentResponse.accounts && ebillEnrolmentResponse.accounts.length > 0) ||
      (ebillEnrolmentResponse.billers && ebillEnrolmentResponse.billers.length > 0) ||
      ebillEnrolmentResponse.authentication ||
      ebillEnrolmentResponse.callBackTime
    );
  }

  get areEbillsAvailable(): boolean {
    return this.isEbillCapable() && !this.isEbillEnabled();
  }

  get areEbillsDue(): boolean {
    return this.isEbillEnabled() && this.hasLatestEbill() && !this.latestEbillPaid();
  }

  get isLatestEbillPaid(): boolean {
    return this.latestEbillPaid();
  }

  get areEbillsEnabledButNotDue(): boolean {
    return this.isEbillEnabled() && !this.hasLatestEbill() && !this.isEbillsStatusInError();
  }

  get ebillLink(): string {
    return this.getLatestEbillURL();
  }

  get showStopEbills(): boolean {
    return this.isEbillEnabled();
  }

  get payeeType(): 'manual' | 'electronic' {
    return (this.payeeSummary as PayeeSummary).electronic ? 'electronic' : 'manual';
  }

  private isEbillsStatusInError(): boolean {
    return !!(this.payeeSummary?.ebill?.status !== 'ACTIVE');
  }

  private ebillErrorEquals(error: string) {
    const ebillSummary = this.payeeSummary?.ebill;
    return !!(ebillSummary && ebillSummary.status === error);
  }

  private hasLatestEbill(): boolean {
    return !!this.payeeSummary?.ebill?.latestBill;
  }

  private latestEbillPaid(): boolean {
    return !!(this.payeeSummary?.ebill?.latestBill?.status === 'PAID');
  }

  private getLatestEbillURL(): string {
    return this.payeeSummary?.ebill?.latestBill?.url || '';
  }

  private isEbillCapable(): boolean {
    return !!this.payeeSummary?.ebill?.capable;
  }

  private isEbillEnabled(): boolean {
    return !!this.payeeSummary?.ebill?.enabled;
  }
}
