import {
  ActivatedRouteSnapshot,
  CanDeactivate,
  Router,
  RouterStateSnapshot,
  UrlCreationOptions,
} from '@angular/router';
import { Location } from '@angular/common';
import { Observable } from 'rxjs';
import { tap } from 'rxjs/operators';
import { BillpayDiscardChangesFormComponent } from './billpay-discard-changes-form-component.model';

export abstract class BillpayBaseDeactivateGuardService implements CanDeactivate<BillpayDiscardChangesFormComponent> {
  protected abstract readonly allowedPreviousRoutes: RegExp[];
  protected abstract readonly allowedNextRoutes: RegExp[];

  /** @internal */
  constructor(protected readonly router: Router, protected readonly location: Location) {}

  canDeactivate(
    component: BillpayDiscardChangesFormComponent,
    currentRoute: ActivatedRouteSnapshot,
    currentState: RouterStateSnapshot,
    nextState: RouterStateSnapshot,
  ): Observable<boolean> | boolean {
    const isNextRouteAllowed = this.allowedNextRoutes.some(route => route.test(nextState.url));
    const isPreviousRouteAllowed = this.allowedPreviousRoutes.some(route => route.test(currentState.url));
    const isNotSameRoute = currentState.url !== nextState.url;
    const isNavigationAllowed = isNextRouteAllowed && isPreviousRouteAllowed && isNotSameRoute;

    return (
      isNavigationAllowed ||
      component.openModal().pipe(
        // TODO: Due to Angular issue https://github.com/angular/angular/issues/13586
        tap(canDeactivate => {
          if (!canDeactivate) {
            const currentUrlTree = this.router.createUrlTree([], currentRoute as UrlCreationOptions);
            this.location.go(currentUrlTree.toString());
          }
        }),
      )
    );
  }
}
